
.hey {
  @include ffo-swap($font-headline);
  @include fluid-type($min-width, $max-width, 48px, 180px);
  color: white;
  font-weight: normal;
  text-rendering: optimizeLegibility;
  line-height: .95;
  margin: 0;
  padding-left: 5px;
  background-image: url(/img/static.gif);
  background-position: center center;
  background-repeat:  no-repeat;
  background-attachment: fixed;
  background-size: cover;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;

  @media (prefers-reduced-motion: reduce) {
    background-image: url(/img/static.jpg);
  }

  @include media(tablet-portrait-up) {
    padding-left: 15px;
  }
}

.panel--hey {
  @include gridsupport{
    display: grid;

    @include media(tablet-portrait-up) {
      align-items: center;
    }
  }
}

.panel--process {
  // background: $darkness;
  background: linear-gradient(90deg, #EB9532 0,#ff624d);
  color: white;;
  position: relative;
  padding: 2rem;

  @include media(tablet-portrait-up) {
    align-items: center;
    padding: 6rem;
  }

  &:before {
    pointer-events: none;
    border: 4px solid white;
    position: absolute;
    content: "";
    top: 1rem;
    right: 1rem;
    bottom: 1rem;
    left: 1rem;

    @include media(tablet-portrait-up) {
      top: 3rem;
      right: 3rem;
      bottom: 3rem;
      left: 3rem;
    }
  }
}

.primary__intro {
  @include fluid-type($min-width, $max-width, 22px, 44px);
}

.primary__intro--buttons {
  @include fluid-type($min-width, $max-width, 22px, 44px);
  position: relative;

  a {
    @include ffo-swap($font-body);
    line-height: inherit;
    background: inherit;
    display: block;
    border: 4px solid;
    padding: .25em .4em;
    text-decoration: none;
    font-weight: bold;
    margin-bottom: 1rem;
    text-align: center;
    color: $linked;

    &:hover {
      color: $white;
      border-color: $gray-20;
      background: linear-gradient(90deg, #EB9532 0,#ff624d);
    }

    @include media(tablet-portrait-up) {
      display: inline-block;
    }
  }
}
